<template>
  <div>
    <!-- 搜索 -->
    <van-search v-model="searchInfo" show-action placeholder="请输入搜索关键词" @search="onSearch" @cancel="onCancel" />

    <!-- 影院 -->
    <div class="cinema-box">
      <van-list v-model="loading" :immediate-check="false" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <div class="cinema-item" v-for="item in searchArr" :key="item.name">
          <div class="item-left">
            <div class="cinema-name">{{ item.name }}</div>
            <div class="cinema-path">{{ item.address }}</div>
          </div>

          <div class="item-right">
            <div class="cinema-price">￥{{ Math.ceil(item.lowPrice / 100) }}元 <span>起</span></div>
            <div class="cinema-distance">距离未知</div>
          </div>
        </div>
      </van-list>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Search',
  data() {
    return {
      searchInfo: '',
      cinemaArr: [],
      loading: false,
      finished: false,
    }
  },
  mounted() {
    this.getSearchList()
  },
  methods: {
    async getSearchList() {
      const { data: res } = await this.$api.GetMoviesCinemaList()
      // console.log(res.cinemas)
      this.cinemaArr = res.cinemas
    },
    onSearch(val) {
      this.searchInfo = val
    },
    onCancel() {
      this.$router.back()
    },
    onLoad() {
      this.onLoad = false
      // 数据全部加载完成

      this.finished = true
    },
  },
  computed: {
    searchArr() {
      return this.cinemaArr.filter((item) => item.name.toUpperCase().includes(this.searchInfo.toUpperCase()) || item.address.toUpperCase().includes(this.searchInfo.toUpperCase()))
    },
  },
}
</script>

<style lang="less" scoped>
.cinema-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 75px;
  // background-color: pink;
  padding: 15px;

  .item-left {
    width: 250px;
    .cinema-name {
      height: 23px;
      color: #191a1b;
      font-size: 15px;
    }
    .cinema-path {
      height: 18px;
      color: #797d82;
      font-size: 12px;
      margin-top: 5px;
    }
  }
  .item-right {
    width: 70px;
    color: #ff5f16;
    .cinema-price {
      height: 23px;
      font-size: 15px;
      span {
        font-size: 10px;
      }
    }
    .cinema-distance {
      font-size: 11px;
      height: 18px;
      color: #797d82;
      margin-top: 5px;
      text-align: center;
    }
  }
}
/deep/.van-icon-arrow-down {
  color: #191a1b;
}
.cinema-box {
  margin-bottom: 20px;
}
</style>
